
:root {
    --background: #0D1B1E;
    --text: #b6c2d9;
    --accent: #828489;
    --effect: #4d7ea8;

}
/* Typography */


@media (min-width: 576px) {
    html {
        font-size: 14px;
    }
}

@media (min-width: 768px) {
    html {
        font-size: 16px;
    }
}

@media (min-width: 992px) {
    html {
        font-size: 18px;
    }
}

@media (min-width: 1200px) {
    html {
        font-size: 20px;
    }
}

.icons-social i {
    font-size: 3em;
    color: #CCCCCC;
}

 .icons-social a {
     color: var(--text);
     text-decoration: none;
 }

 .icons-social a:hover {
     color: var(--effect);
 }

 .social-icon {
     margin-left: 20px;
     margin-right: 20px;
     padding-top: 1em;
 }
/* Custom Styles */

main {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    justify-content: center;
    padding: 0 30px;
    text-align: center;
}

main>.intro {
    font-size: 3.75em;
    font-weight: 600;
}

main>.tagline {
    font-size: 1.5rem;
    margin: 1.5rem 0;
    font-weight: 100;
}

.icons-social i {
    padding: 10px;
}

.icons-social a {
    text-decoration: none;
}

.devto {
    margin-bottom: -0.20rem;
}

.devto svg {
    margin-bottom: -0.20rem;
    margin-left: 0.675rem;
    ;
    width: 2.65rem;
    height: 2.65rem;
}

.svg-inline--fa{
    height: 3em;
}

ul.no-bullets {
    list-style: none;
}

ul {
    list-style: none;
}